html, body, #root {
  font: 0.9rem sans-serif;
  background: #0a1f44;
  color: #1e2432;
  height: 100%;
  margin: 0;
}

.main-container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.search-container{
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 2rem;
  border-radius: 20px;
}

button{
  padding: 1rem;
  border: none;
  background-color: rgba(250, 250, 250, 0.85);
  cursor: pointer;
  border-radius: 0px 20px 20px 0px;
}

.search-icon{
  font-size: 1.5rem;
  
}

.search {
  padding: 1.5rem 2.3rem ;
  
  outline: none;
  border: none;
  border-radius: 20px 0px 0px 20px;
  background: rgba(250, 250, 250, 0.85);
  
}

.city {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 1.5rem 2.3rem;
  border-radius: 20px;
  min-width: 15rem;
  
  background: rgba(250, 250, 250, 0.85);
  box-shadow: 10px 10px 5px 0px rgba(15, 15, 15, 0.404);
}

p {
  margin-top: 10px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.city-temp {
  font-size: 5rem;
  font-weight: bold;
  margin-top: 10px;
  color: #1e2432;
  text-align: center;
}

.city sup {
  font-size: 0.5em;
}

.city-name {
  font-size: 2em;
}

.city-name sup {
  padding: 0.2em 0.6em;
  margin-left: 0.2em;
  border-radius: 30px;
  color: #fff;
  background: #ff8c00;
}

.city-icon {
  margin-top: 10px;
  width: 100px;
  height: 100px;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

